import { AppLoaderPlugin } from "@pixi/loaders";
import { SpritesheetLoader } from "@pixi/spritesheet";
import { BitmapFontLoader } from "@pixi/text-bitmap";
import { Application } from "@pixi/app";
import { Loader } from "@pixi/loaders";
import { StateManager } from "./states/StateManager";
import { settings, Texture } from "pixi.js";
import { Container, DisplayObject } from "@pixi/display";
import { Sprite } from "@pixi/sprite";
import { LoadingPopup } from "./LoadingPopup";
import p2, { ContactMaterial, Material } from "p2";


Application.registerPlugin(AppLoaderPlugin);
Loader.registerPlugin(SpritesheetLoader);
Loader.registerPlugin(BitmapFontLoader);

export let world: p2.World;
export let interaction: any;
export let app: Application;
export let unusedBodies: p2.Body[] = [];
export let gameSettings = {
    started: false,
    paused: false,
    scale: 1,
    realHeight: 1,
    originalWidth: 1,
    originalHeight: 1,
    material: undefined
};
export let stateManager: StateManager;
export const METER_SIZE = 50;

let loading: LoadingPopup;
export const showLoading = () => {
  if (loading) {
    loading.show();
  }
}
export const hideLoading = () => {
  if (loading) {
    loading.hide();
  }
}

export abstract class BaseGame {
    constructor(width: number, height: number) {
        const options = {
            width: width,
            height: height,
            autoDensity: true,
            resolution: Math.min(Math.floor(window.devicePixelRatio), 3),
          };
          gameSettings.originalWidth = width;
          gameSettings.originalHeight = height;
          app = new Application(options);
          document.body.appendChild(app.view);
          settings.FAIL_IF_MAJOR_PERFORMANCE_CAVEAT = false;
          app.view.style.left = "0%";
          app.view.style.top = "0%";
          app.view.style.width = "100%";
          app.view.style.height = "100%";
          interaction = app.renderer.plugins.interaction;
          app.stage.interactive = true;
          app.ticker.maxFPS = 60;
          const stage = new Container();
          app.stage.addChild(stage);
          world = new p2.World({gravity:[0, -10]});
          this.initContactListener();
          const material = new Material();
          gameSettings.material = material;
          world.addContactMaterial(new ContactMaterial(material, material, {restitution: 0}));
          world.emitImpactEvent = false;
          app.ticker.add(() => {
            if (gameSettings.paused) return;
            world.step(1 / 60, 1 / 10, 3);
            world.bodies.forEach((body)=> {
              const sprite: DisplayObject = (body as any).userData;
              if (sprite && sprite.transform) {
                const v2 = body.position;
                sprite.position.set(v2[0] * METER_SIZE, v2[1] * METER_SIZE);
                sprite.rotation = body.angle;
              }
            });
            this.customLogic();
          });
          this.start(app, stage);
          loading = new LoadingPopup(width, height);
          loading.hide();
          stage.addChild(loading);
    }
    protected customLogic() {
        unusedBodies.forEach((body) => world.removeBody(body));
        this.clearBodyArray();
    }
    protected clearBodyArray(){
      unusedBodies = [];
    }
    private async start(app: Application, stage: Container) {
        const backC = new Container();
        stage.addChild(backC);
        const game = new Container();
        stage.addChild(game);
        stateManager = new StateManager(game);
        await this.load();
        const back = Sprite.from(Texture.WHITE);
        back.width = 1080;
        back.height = 1920;
        backC.addChild(back);

        this.createStates();
    
        const calculateCanvasSize = () => {
          return { width: innerWidth, height: innerHeight };
        };
        const resize = () => {
          const { width, height } = calculateCanvasSize();
          app.renderer.resize(width, height);
          const scaleBack =
            width / height > 1080 / 1920 ? width / 1080 : height / 1920;
          backC.scale.set(scaleBack, scaleBack);
          backC.position.set(
            width * 0.5 - backC.width * 0.5,
            height * 0.5 - backC.height * 0.5
          );
    
          gameSettings.realHeight = height;
          loading.height = height;
          loading.scale.x = loading.scale.y;
    
          const scale = height / 1920;
          gameSettings.scale = scale;
          game.scale.set(scale, scale);
    
          game.position.set(
            backC.position.x + backC.width * 0.5,
            backC.position.y + backC.height * 0.5
          );
          loading.position.set(
            backC.position.x + backC.width * 0.5,
            backC.position.y + backC.height * 0.5
          );
        };
        window.addEventListener("resize", resize);
        resize();
    }
    protected abstract initContactListener();
    protected abstract createStates();
    protected abstract load(): Promise<void>;
}