import { DEG_TO_RAD } from "@pixi/math";
import { Sprite } from "@pixi/sprite";
import { Body, Box } from "p2";
import { Texture } from "pixi.js";
import { METER_SIZE } from "../../../core/BaseGame";
import { Block } from "./Block";

export class StaticBlock extends Block {
    constructor(x: number, y: number, width: number, height: number, rot: number) {
        const view = new Sprite(Texture.WHITE);
        view.anchor.set(0.5);
        view.tint = 0x000000;
        view.width = width;
        view.height = height;
        super(view);

        const shape = new Box({width: width/METER_SIZE, height:height/METER_SIZE});
        this.createBody(shape,Body.STATIC,[x / METER_SIZE, y / METER_SIZE]);
        this.body.mass = 0;
        this.body.angle = DEG_TO_RAD * rot;
        this.type = 0;
    }
}