import { live, sendError } from "../..";
import { sounds } from "../../core/SoundManager";
import { hideLoading, showLoading } from "../BaseGame";
import { DummySocial } from "./social/DummySocial";
import { FBSocial } from "./social/FBSocial";
import { PokiSocial } from "./social/PokiSocial";
import { VKSocial } from "./social/VKSocial";
import { YASocial } from "./social/YASocial";
export enum AdType {
    RewardedVideo = 2,
    Interstitial = 4,
}
export interface ILeaderboardEntry {
    rank: number;
    score: number;
    highscore?: number;
    player: IPlayerEntry;
    hasBonus?: boolean;
    friend?: boolean;
}
export interface IPlayerEntry {
    name: string; 
    photo: string; 
    player_id: string;
}
export interface IInited {
    success: boolean, 
    gameKey: string, 
    secretKey: string, 
    language: string
}
export interface ISocial {
    init(): Promise<IInited>;
    setStorageData(key: string, value: string): Promise<void>;
    getStorageData(key: string, value?: string): Promise<string>;
    setScore(value: number): Promise<void>;
    getScore(): Promise<number>;
    getPlayerEntry(): IPlayerEntry;
    getFriends(): Promise<ILeaderboardEntry[]>;
    showInter(): Promise<void>;
    showReward(): Promise<boolean>;
    showReward(): Promise<boolean>;
    performHapticFeedback(): void;
    startLoading(): void;
    finishLoading(): Promise<void>;
    loadingProgress(p: number): void;
    shareScore(txt: string): void;
    showInvitePage(): void;
    createShortcut(): Promise<void>;
    showGlobalLeaders(score: number): void;
    gameplayStart(): void;
    gameplayStop(): void;
    shouldShareShow(): boolean;
    shouldLeadersShow(): boolean;
    shouldScalePlayer(): boolean;
    shouldPlayFirstAds(): boolean;
    atatappLink(): string;
}
const getLocalStorage = () => {
    try {
        return localStorage;
    } catch (ex) {
        return undefined;
    }
};
let instance: ISocial;
let firstPlay: boolean = true;
export const initSocial = async(): Promise<IInited> => {
    return new Promise<IInited>((resolve, reject)=> {
        /*let t = window.setTimeout(()=> {
            resolve({success: false, gameKey: "72f259223556fc5ce732f0212865799b", secretKey: "7e2cf87503012b4c2e1f2346c99ac6e9c411b5ff", language: "en"});
        }, 5000);*/
        if (process.env.NET === "2") {
            instance = new VKSocial();
        } else if (process.env.NET === "1") {
            instance = new FBSocial();
        } else if (process.env.NET === "3") {
            instance = new YASocial();
        } else if (process.env.NET === "4") {
            instance = new PokiSocial();
        } else {
            instance = new DummySocial();
        }
        if (instance) {
            instance.init().then((b)=> {
                //window.clearTimeout(t);
                resolve(b);
            }).catch((r)=>{
                sendError({"error":"initSocial"});
                reject();
            });
        }
    });
}
export const setScore = async(score: number) => {
    const s = await getScore();
    if (score > s) {
        getLocalStorage()?.setItem("highscore", score.toString());
    }
    await instance.setScore(score);
}
export const getScore = async(): Promise<number> => {
    let s = await instance.getScore();
    if (!s || s === 0) {
        s = parseInt(getLocalStorage()?.getItem("highscore")) || 0;
    }
    return s;
}
export const setStorageData = async(key: string, value: string): Promise<void> => {
    getLocalStorage()?.setItem(key, value);
    await instance.setStorageData(key, value);
}
export const getStorageData = async(key: string, value?: string): Promise<string> => {
    let s = await instance.getStorageData(key, value);
    if (s === value) {
        s = getLocalStorage()?.getItem(key) || value;
    }
    return s;
}
export const getPlayerEntry = (): IPlayerEntry => {
    let p = instance.getPlayerEntry();
    if (!p) {
        p = {
            name: "You",
            player_id: "999",
            photo: "assets/images/player26.jpg",
        }
    }
    return p;
}
export const getFriends = async(): Promise<ILeaderboardEntry[]> => {
    const p = [];
    for (let i = 0; i < 26; i++) {
        p.push(`assets/images/player${i}.jpg`);
    }
    let score = 0;
    const r:ILeaderboardEntry[] = await instance.getFriends();
    const l = 19 - r.length;
    for (let i = 0; i < l; i++) {
        score += Math.floor(Math.random()*600) + 100;
        const entry = {rank: 1, score: score, player: {name: `Player${i}`, photo: p.splice(Math.floor(Math.random()*p.length),1)[0], player_id: i.toString()}};
        r.push(entry);
    }
    return r;
}
export const showADS = async(type: AdType): Promise<boolean> => {
    let r = false;
    showLoading();
    const mutedMusic = sounds.muteMusic;
    const muteSounds = sounds.muteSounds;
    sounds.muteMusic = true;
    sounds.muteSounds = true;
    switch (type) {
        case AdType.Interstitial:
            await instance.showInter();
            r = true;
            break;
        case AdType.RewardedVideo:
            r = await instance.showReward();
            break;
    }
    firstPlay = false;
    hideLoading();
    sounds.muteMusic = mutedMusic;
    sounds.muteSounds = muteSounds;
    return r;
}
export const performHapticFeedback = () => {
    instance.performHapticFeedback();
}
export const startLoading = () => {
    instance.startLoading();
}
export const loadingProgress = (p) => {
    instance.loadingProgress(p);
}
export const finishLoading = async() => {
    return instance.finishLoading();
}
export const shouldScalePlayer = (): boolean => {
    return live && instance.shouldScalePlayer();
}
export const shouldShareShow = (): boolean => {
    return instance.shouldShareShow();
}
export const shouldLeadersShow = (): boolean => {
    return instance.shouldLeadersShow();
}
export const shouldPlayFirstAds = (): boolean => {
    return firstPlay && instance.shouldPlayFirstAds();
}
export const shareScore = (txt: string) => {
    instance.shareScore(txt);
}
export const showInvitePage = () => {
    instance.showInvitePage();
}
export const createShortcut = async()=> {
    if (!firstPlay) return;
    return await instance.createShortcut();
}
export const gameplayStart = () => {
    instance.gameplayStart();
}
export const gameplayStop = () => {
    instance.gameplayStop();
}
export const atatappLink = () => {
    return instance.atatappLink();
}
export const showGlobalLeaders = (score: number) => {
    instance.showGlobalLeaders(score);
}