import { TextedButton } from "../../../core/ui/TextedButton";

export class PowerupButton extends TextedButton {
    public id: number;
    private a: number;
    constructor(amount: number, color: number, textColor: number) {
        super(amount.toString(), 30, color, textColor);
        this.a = amount;
    }
    public set amount(i: number) {
        this.a = i;
        this.text = this.a.toString();
    }
    public get amount(): number {
        return this.a;
    }
}