import { Graphics, Sprite, Texture } from "pixi.js";
import { gameSettings, METER_SIZE, world } from "../../../core/BaseGame";
import { Block } from "./Block";
import gsap from "gsap";
import { Body, Circle } from "p2";

export class CircleBlock extends Block {
    //private static colors = [0xCCCC00,0xFF0000,0xFF00FF,0xAAFF00,0x00FFFF,0xFFFF00,0x252994,0xebab34];
    private static radius = 35;
    constructor(x: number, y: number, type: number, create: boolean = true) {
        const radius = (type != Block.goalType) ? CircleBlock.radius : CircleBlock.radius*1.5;
        const textureName = (type != Block.goalType) ? `${type}.png` : `hero.png`;
        const view = new Sprite(Texture.from(textureName));
        if (type != Block.goalType) {
            view.scale.set(0.5,0.5);
        }
        super(view);
        this.position.set(x,y);
        if (create) {
            this.createCircleBody(radius);
        }
        this.type = type;
    }
    public createCircleBody(radius: number = CircleBlock.radius) {
        const shape = new Circle({radius: radius / METER_SIZE});
        shape.material = gameSettings.material;
        this.createBody(shape,Body.DYNAMIC,[this.position.x / METER_SIZE, this.position.y / METER_SIZE]);
    }
    public increase(s: number, fromBoom: boolean = false) {
        super.increase(s, fromBoom);
        if (this.body && this.steps < Block.stepsAmount) {
            const i = Block.scaleFactor * this.steps;
            //const r = i * CircleBlock.radius;
            gsap.to(this.view.scale, {x: i, y: i, duration: 0.2, onUpdate: ()=> {
                (this.shape as Circle).radius = CircleBlock.radius / Block.scaleFactor * this.view.scale.x / METER_SIZE;
                this.shape.updateBoundingRadius();
                this.body.updateBoundingRadius();
                this.body.updateMassProperties();
                this.busy = false;
            }});
        }
    }
}