import gsap from "gsap";
import { Container, Graphics } from "pixi.js";

export class Explosion extends Container{
    constructor() {
        super();
        const g = new Graphics();
        g.beginFill(0xFF0000, 1);
        g.drawCircle(0, 0, 70);
        this.addChild(g);
        this.scale.set(0,0);
        this.animate();
    }
    public animate() {
        const duration = 0.3;
        gsap.to(this.scale, {x: 1.5, y: 1.5, duration, onComplete: ()=> {
            this.destroy();
        }});
        gsap.to(this, {alpha: 0, delay:duration*0.4, duration: duration*0.6});
    }
}