import { Container } from "@pixi/display";
import { Sprite } from "@pixi/sprite";
import p2, { Body, Shape } from "p2";
import { unusedBodies, world } from "./BaseGame";

export class PhysicsSprite extends Container {
    public body: p2.Body;
    public shape : p2.Shape;
    protected view: Sprite;
    
    constructor(view: Sprite) {
        super();
        this.view = view;
        this.view.anchor.set(0.5, 0.5);
        this.addChild(this.view);
    }
    public createBody(shape: Shape, type: 1|2|4, position: [number, number] = [0,0]) {
        this.view.alpha = 1;
        this.shape = shape;
        this.body = new Body({type, position, mass: 5, damping: 0, angularDamping: 0});
        this.body.addShape(shape);
        world.addBody(this.body);
        (this.body as any).userData = this;
    }
    public clean() {
        this.destroyBody();
        this.parent && this.parent.removeChild(this);
    }
    protected destroyBody (immidiately: boolean = false) {
        if (this.body) {
            if (immidiately) {
                world.removeBody(this.body);
            } else {
                unusedBodies.push(this.body);
            }
            this.body = undefined;
        }
    }
    
}