import { Text } from "pixi.js";
import { lang } from "../..";
import { Button } from "./Button";

export class TextedButton extends Button {
    private txt: Text;
    constructor(text: string, size: number, color: number, textColor: number) {
        super("", "");

        this.txt = new Text(text, {fontFamily: "Arial", fontSize: size, fill: textColor});
        this.txt.anchor.set(0.5,0.5);
        this.addChild(this.txt);
        this.btn.tint = color;
        //this.btn.alpha = 0.001;
        this.btn.width = this.txt.width + 100;
        this.btn.height = this.txt.height + 50;
    }
    public set text(t: string) {
        this.txt.text = t;
    }
}