import {
  sendAdEvent,
  EGAAdAction,
  EGAAdType,
  EGAAdError,
  sendError,
  live,
} from "../../..";
import { IInited, ILeaderboardEntry, IPlayerEntry, ISocial } from "../SocialManager";
let yasdk: any;
let yaPlayer: any;
let leaders: any;
export class YASocial implements ISocial {
  private player: IPlayerEntry;
  public showGlobalLeaders(score: number) {
    //nothing
  }
  public shouldLeadersShow(): boolean {
    return false;
  }
  public atatappLink(): string {
    return "";
  }
  public shouldPlayFirstAds(): boolean {
    return false;
  }
  public shouldShareShow(): boolean {
    return false;
  }
  public shouldScalePlayer(): boolean {
    return true;
  }
  public gameplayStart(): void {
    //nothing
  }
  public gameplayStop(): void {
    //nothing
  }
  public async createShortcut(): Promise<void> {
    if (!live) return; 
    return new Promise<void>((resolve) => {
      yasdk.feedback.canReview().then(({ value, reason }) => {
        if (value) {
          yasdk.feedback.requestReview().then(({ feedbackSent }) => {
            resolve();
          });
        } else {
          resolve();
        }
      });
    });
  }
  public showInvitePage(): void {
    //nothing
  }
  public shareScore(txt: string): void {
    //nothing
  }
  public startLoading(): void {
    //nothing
  }
  public async finishLoading() {
    return;
  }
  public loadingProgress(p: number): void {
    //nothing
  }
  public performHapticFeedback() {
    //nothing
  }
  public async getFriends(): Promise<ILeaderboardEntry[]> {
    if (!live) return []; 
    const r = [];
    leaders = await yasdk.getLeaderboards("main");
    console.log(leaders);
    const res = await leaders.getLeaderboardEntries("main", {
      quantityTop: 20,
      includeUser: false,
      quantityAround: 1,
    });
    console.log(res);
    if (res && res.entries.length > 0) {
      for (let e of res.entries) {
        const entry = {
          rank: 1,
          score: e.score,
          player: {
            name: e.player.publicName,
            photo: e.player.getAvatarSrc("large"),
            player_id: e.player.uniqueID,
          },
        };
        r.push(entry);
      }
    }
    return r;
  }
  public getPlayerEntry(): IPlayerEntry {
    if (!live) return; 
    if (!this.player && yaPlayer) {
      this.player = {
        name: yaPlayer.getName(),
        player_id: yaPlayer.getUniqueID(),
        photo: yaPlayer.getPhoto("large"),
      };
    }
    return this.player;
  }
  public async setScore(value: number): Promise<void> {
    if (!live) return; 
    await yaPlayer.setStats({ score: value });
    leaders.setLeaderboardScore("main", value);
  }
  public async getScore(): Promise<number> {
    if (!live) return 0; 
    const data = await yaPlayer.getStats(["score"]);
    let score = 0;
    if (!data || (data && !data.score)) {
      score = 0;
      await this.setScore(0);
    } else {
      score = data.score;
    }
    return score;
  }
  public async setStorageData(key: string, value: string): Promise<void> {
      if (!live) return; 
    await yaPlayer.setData({ [key]: value });
  }
  public async getStorageData(key: string, value?: string): Promise<string> {
       if (!live) return value; 
    const current = await yaPlayer.getData([key]);
    if (!current) {
      await yaPlayer.setData({ [key]: value });
      return value;
    } else {
      return current[key] || value;
    }
  }
  public async init(): Promise<IInited> {
      let success = false;
      yasdk = await YaGames.init();
      try {
        yaPlayer = await yasdk.getPlayer();
        leaders = await yasdk.getLeaderboards("main");
        success = true;
      } catch(e) {
        
      }
    const language = yasdk.environment.i18n.lang === "ru" ? "ru" : "en";
    return {
      success,
      language,
      gameKey: "fe62158b9e723b4ef28865b07e79b439",
      secretKey: "65d9709dc8029d19ab3694b15307320430627f17",
    };
  }
  public async showInter(): Promise<void> {
    await new Promise<void>((resolve) => {
      yasdk.adv.showFullscreenAdv({
        callbacks: {
          onClose: function (wasShown) {
            sendAdEvent(
              wasShown ? EGAAdAction.Show : EGAAdAction.FailedShow,
              EGAAdType.Interstitial
            );
            resolve();
          },
          onError: function (e) {
            sendAdEvent(
              EGAAdAction.FailedShow,
              EGAAdType.Interstitial,
              EGAAdError.Unknown
            );
            sendError(e);
            resolve();
          },
        },
      });
    });
  }
  public async showReward(): Promise<boolean> {
    let r = false;
    try {
      r = await new Promise<boolean>((resolve) => {
        yasdk.adv.showRewardedVideo({
          callbacks: {
            onRewarded: () => {
              sendAdEvent(EGAAdAction.RewardReceived, EGAAdType.RewardedVideo);
              resolve(true);
            },
            onError: (e) => {
              sendAdEvent(
                EGAAdAction.FailedShow,
                EGAAdType.RewardedVideo,
                EGAAdError.Unknown
              );
              sendError(e);
              resolve(false);
            },
          },
        });
      });
    } catch (e) {
      sendAdEvent(
        EGAAdAction.FailedShow,
        EGAAdType.RewardedVideo,
        EGAAdError.Unknown
      );
      sendError(e);
      r = false;
    }
    return r;
  }
}
