import { Texture } from "@pixi/core";
import { Container } from "@pixi/display";
import { Sprite } from "@pixi/sprite";
import gsap, { Linear } from "gsap";

export class LoadingPopup extends Container {
    private sprite: Sprite;
    constructor(width: number, height: number) {
        super();
        const back = Sprite.from(Texture.WHITE);
        back.anchor.set(0.5);
        back.tint = 0;
        back.alpha =0.6;
        back.width = width*2;
        back.height = height*2;
        back.interactive = true;
        this.addChild(back);

        this.sprite = Sprite.from("assets/preloader.png");
        this.sprite.anchor.set(0.5,0.5);
        this.addChild(this.sprite);
    }
    public show() {
        this.visible = true;
        this.rotate();
    }
    public hide() {
        this.visible = false;
        gsap.killTweensOf(this.sprite);
    }
    private rotate() {
        this.sprite.rotation = 0;
        gsap.to(this.sprite, {rotation: Math.PI*2, duration: 5, ease: Linear.easeNone, onComplete: ()=> {
            this.rotate();
        }});
    }
}