import { Howl } from "howler";
export class SoundManager {
    private path: string;
    private all: { [key: string]: Howl };
    private musics: { [key: string]: Howl };
    private mutedSounds: boolean;
    private mutedMusic: boolean;
    public init(path: string) {
        this.path = path;
        this.all = {};
        this.musics = {};
        this.mutedSounds = false;
        this.mutedMusic = false;
    }
    public set muteMusic(m: boolean) {
        this.mutedMusic = m;
        this.musics["music"].mute(m);
    }
    public set muteSounds(m: boolean) {
        this.mutedSounds = m;
        for (const s in this.all) {
            if (this.all[s]) {
                this.all[s].mute(m);
            }
        }
    }
    public get muteSounds() {
        return this.mutedSounds;
    }
    public get muteMusic() {
        return this.mutedMusic;
    }
    
    public add(name: string, settings?: { autoplay?: boolean; loop?: boolean; volume: number }) {
        if (this.all[name]) return;
        if (!this.path) {
            throw new Error("setup path for sounds");
        }
        const src: string[] = ["mp3", "ogg"].map((value) => {
            return `${this.path}/${name}.${value}`;
        });
        
        const m = new Howl({
            src,
            autoplay: settings?.autoplay,
            loop: settings?.loop,
            volume: settings?.volume || 1,
        });

        if (name !== "music") {
            this.all[name] = m;
        } else {
            this.musics[name] = m;
        }
    }
    public play(name: string) {
        if (!this.all[name]) {
            console.error("no sound was found");
            return;
            //throw new Error("no sound was found");
        }
        this.all[name].play();
    }
}
export const sounds = new SoundManager();