import { Texture } from "@pixi/core";
import { Container, IDestroyOptions } from "@pixi/display";
import { Sprite } from "@pixi/sprite";
import { sounds } from "../SoundManager";

export class Button extends Container {
    private normalTexture: Texture;
    private downTexture: Texture;
    private isDisabled: boolean = false;
    private isDown: boolean;
    protected btn: Sprite;

    constructor(normalState: string, downState: string) {
        super();
        this.normalTexture = (normalState === "") ? Texture.WHITE : Texture.from(normalState);
        this.downTexture = (normalState === "") ? Texture.WHITE : Texture.from(downState);
        this.btn = Sprite.from(this.normalTexture);
        this.btn.anchor.set(0.5,0.5);
        this.addChild(this.btn);

        this.on("pointerdown", this.onButtonDown.bind(this))
            .on("pointerup", this.onButtonUp.bind(this))
            .on("pointerupoutside", this.onButtonUp.bind(this))
            .on("pointerout", this.onButtonOut.bind(this));

        this.disabled = false;
    }
    public set disabled(value: boolean) {
        this.isDisabled = value;
        if (this.isDisabled) {
            this.interactive = false;
            this.buttonMode = false;
            this.btn.alpha = 0.5;
        } else {
            this.interactive = true;
            this.buttonMode = true;
            this.btn.alpha = 1;
        }
    }
    public get disabled(): boolean {
        return this.isDisabled;
    }
    public onButtonOut() {
        if (this.isDisabled) {
            return;
        }
        if (this.isDown) {
            return;
        }
        this.btn.texture = this.normalTexture;
    }

    protected onButtonDown(e: any) {
        if (this.isDisabled) {
            return;
        }
        sounds.play("click");
        this.isDown = true;
        this.btn.texture = this.downTexture;
    }
    protected onButtonUp() {
        if (this.isDisabled) {
            return;
        }
        this.isDown = false;
        this.btn.texture = this.normalTexture;
    }
    override destroy(options?: boolean | IDestroyOptions): void {
        this.off("pointerdown")
            .off("pointerup")
            .off("pointerupoutside")
            .off("pointerout");
        super.destroy(options);
    }
}