import { Container } from "@pixi/display";
import { BaseState } from "./BaseState";

export class StateManager {
    private states: Map<{new(): BaseState}, BaseState>;
    private currentState: BaseState;
    private stage: Container;
    constructor(stage: Container) {
        this.stage = stage;
        this.states = new Map<{new(): BaseState}, BaseState>();
    }
    public get activeState() {
        return this.currentState;
    }
    public add<T extends BaseState>(state: T):void {
        this.states.set(<typeof BaseState>state.constructor as unknown as { new(): T}, state);
        state.create();
    }
    public start<T extends BaseState>(c: { new(): T}, ...params: any): void {
        const state  = this.states.get(c);
        if (state) {
            if ( this.currentState) {
                this.currentState.close();
                this.stage.removeChild(this.currentState);
            }
            state.start(params);
            this.currentState = state;
            this.stage.addChild(this.currentState);
        } else {
            throw new Error(`No state ${c}`);
        }
    }
    public restart(...params: any) {
        const state = this.currentState;
        if ( this.currentState) {
            this.currentState.close();
            this.stage.removeChild(this.currentState);
        }
        window.setTimeout(()=> {
            state.start(params);
            this.currentState = state;
            this.stage.addChild(this.currentState);
        },100);
        
    }
}