import { Container, Graphics, ObservablePoint, Point, Ticker } from "pixi.js";
import { EGAProgressionStatus, sendEvent, sendProgressionEvent } from "../..";
import { app, gameSettings, METER_SIZE, stateManager, world } from "../../core/BaseGame";
import { gameplayStart } from "../../core/natives/SocialManager";
import { BaseState } from "../../core/states/BaseState";
import { Block } from "./game/Block";
import { CircleBlock } from "./game/CircleBlock";
import { StaticBlock } from "./game/StaticBlock";
import gsap, {Linear} from "gsap";
import { Explosion } from "./game/Explosion";
import { Body } from "p2";
import { RocketPowerup } from "./game/RocketPowerup";
import { GoalBlock } from "./game/GoalBlock";
import { MenuState } from "./MenuState";
import { TextedButton } from "../../core/ui/TextedButton";
import { PowerupButton } from "./game/PowerupButton";

export class GameViewState extends BaseState {
    private moveBox: Block;
    private staticBox: Block;
    private bottomOpenWall: Block;
    private activeBlock: CircleBlock;
    private maxY: number;
    private level: number;
    private tick = 0;
    private moving: boolean;
    private isActivePowerup: boolean;
    private preview: Graphics;
    private mousePressed: boolean;
    private nextLines: number[][];
    private previewBlocks: Container;
    private nextType: number;
    private nextBlock: CircleBlock;
    private isTransition: boolean;
    private powerup1: PowerupButton;
    private powerup2: PowerupButton;
    private powerups: number[];
    private powerupButtons: PowerupButton[];

    public create(): void {
        this.powerup1 = new PowerupButton(0, 0x000000, 0xFFFFFF);
        this.powerup1.id = 0;
        this.powerup1.position.set(-390, 900);
        this.addChild(this.powerup1);
        this.powerup1.on("pointerup", () => {
            this.startPowerup(this.powerup1.id);
        });

        this.powerup2 = new PowerupButton(0, 0xFF0000, 0x000000);
        this.powerup2.id = 1;
        this.powerup2.position.set(-265, 900);
        this.addChild(this.powerup2);
        this.powerup2.on("pointerup", () => {
            this.startPowerup(this.powerup2.id);
        });
        this.powerupButtons = [];
        this.powerupButtons.push(this.powerup1);
        this.powerupButtons.push(this.powerup2);

        this.preview = new Graphics();
        this.addChild(this.preview);

        this.previewBlocks = new Container();
        this.addChild(this.previewBlocks);

        const line = new Graphics();
        line.lineStyle(4, 0x000000, 0.3);
        line.moveTo(-400, 500);
        line.lineTo(400, 500);
        this.addChild(line);
    }
    public async start(...params: any) {
        gameplayStart();
        this.powerups = [2, 3];
        for (let i = 0; i < this.powerupButtons.length; i++) {
            this.powerupButtons[i].amount = this.powerups[i];
        }
        this.isTransition = false;
        this.nextType = undefined;
        this.nextLines = [];
        this.isActivePowerup = false;
        this.level = 4;
        this.generateLineBlocks(this.level - 3);
        this.tick = 0;
        this.maxY=0;
        this.moving = false;
        sendEvent("start", "game", "level");
        sendProgressionEvent(EGAProgressionStatus.Start);
        gameSettings.paused = false;

        await this.generateStartBlocks();
        this.addActiveBlock();
        app.stage.on("pointerup", this.onClick, this);
        app.stage.on("pointerdown", this.onTapStart, this);
        Ticker.shared.add(this.update, this);
    }
    public createExplosion(position: ObservablePoint) {
        const e = new Explosion();
        e.position.copyFrom(position);
        this.addChild(e);
      }
      public levelComplete() {
          this.isTransition = true;
          this.moving = false;
          this.previewBlocks.children.forEach((b)=> {
            (b as CircleBlock).createCircleBody();
            this.addChild(b);
          });
          this.previewBlocks.removeChildren();
          this.bottomOpenWall.animateAndDestroy();
          this.moveBox.animateAndDestroy();
          this.staticBox.animateAndDestroy();
          this.nextBlock.clean();
          this.activeBlock.clean();
          this.mousePressed = false;
      }
    public moveBalls() {
        if (this.isTransition) return;
        this.moving = true;
        const l = this.nextLines.length;
        const pos = this.moveBox.body.position;
        const m = {x:pos[0], y:pos[1]};
        gsap.to(m, {y: (-500 + l*70)/METER_SIZE, delay: 0.1, duration:1, ease: Linear.easeNone, onUpdate: ()=> {
            this.moveBox.body.position = [m.x, m.y];
        }, onComplete: ()=> {
            this.moveBox.body.position = [0, -575/METER_SIZE];
            this.addLineBlocks();
            this.generateLineBlocks(this.level - 3);
            this.moving = false;
        }});
    }
    private update() {
        if (gameSettings.paused) return;
        this.drawMouse();
        let wasEnd = false;
        let wasGameOver = false;
        let goal: GoalBlock = undefined;
        world.bodies.forEach((body)=> {
            const pos = body.position;
            if (body.type === Body.KINEMATIC && body !== this.moveBox.body) {
                if (pos[0] < -315/METER_SIZE || pos[0] > 315/METER_SIZE) {
                    body.position = [(pos[0] < 0) ? -315/METER_SIZE : 315/METER_SIZE, pos[1]];
                    body.velocity[0] *= -1;
                } else if (pos[1] < -455 / METER_SIZE || pos[1] > 660 / METER_SIZE) {
                    body.position = [pos[0], (pos[1] < -455/METER_SIZE) ? -455/METER_SIZE : 660/METER_SIZE];
                    body.type = Body.DYNAMIC;
                }
            }
            if (!this.isTransition && body.type === Body.DYNAMIC && pos[1] > this.maxY) {
                this.maxY = pos[1];
            }
            if (this.isTransition && body.type === Body.DYNAMIC && pos[1] < -600/METER_SIZE) {
                wasEnd = true;
            }
            if (this.isTransition && body.type === Body.DYNAMIC && pos[1] < -1800/METER_SIZE) {
                ((body as any).userData as Block).clean(); 
            }
            if (body.type === Body.DYNAMIC && (body as any).userData && (body as any).userData.type === Block.goalType ) {
                goal = (body as any).userData;
                if (!this.isTransition && pos[1] > 450/METER_SIZE) {
                    wasGameOver = true;
                }
            }
        });
        if (goal && !wasGameOver) {
            goal.resetDeathCounter();
        }
        if (this.isTransition && !wasEnd) {
            this.tick = 0;
            this.level+=1;
            if (this.level > 7) {
                this.level = 7;
            }
            this.createMoveWalls();
            this.generateNewBlocks().then(()=> {
                this.generateLineBlocks(this.level - 3);
                this.addActiveBlock();
            });
            this.isTransition = false;
        }
        if (this.moving || this.isTransition) return;
        if (goal && wasGameOver) {
            goal.startCountDeath();
        }
        this.tick+=1;
        
        if (this.tick%((this.level-3)*700) === 0) {
            if (this.tick >= (this.level-3) * 2100) {
                this.tick = 0;
                this.level+=1;
                if (this.level > 7) {
                    this.level = 7;
                }
            }
            this.moveBalls();
        }
    }
    private async startPowerup(id: number) {
        console.log(id)
        if (this.isActivePowerup || gameSettings.paused || this.isTransition || this.powerups[id] <= 0) return;
        this.isActivePowerup = true;
        switch(id) {
            case 0:
                await this.startVerticalRocket();
                break;
            case 1:
                await this.startHorizontalRocket();
                break; 
        }
        this.powerups[id] -= 1;
        this.powerupButtons[id].amount = this.powerups[id];
        this.isActivePowerup = false;
    }
    private async startVerticalRocket() {
        const x = Math.random() * 560 - 280;
        await this.startRocket(x, 650, x, -450);
    }
    private async startHorizontalRocket() {
        const h = this.maxY + 400;
        const y = Math.random() * h - 400;
        await this.startRocket(-280, y, 280, y);
    }
    private async startRocket(startX: number, startY: number, endX: number, endY: number) {
        const rocket = new RocketPowerup(startX, startY, endX, endY);
        this.addChild(rocket);
        await rocket.animate();
    }
    private generateActiveBlock() {
        if (this.nextBlock) {
            this.removeChild(this.nextBlock);
        }
        this.nextType = Math.floor(Math.random() * (this.nextLines.length + 3)) + 1;
        this.nextBlock = new CircleBlock(-90, 650, this.nextType, false);
        this.nextBlock.scale.set(0.7);
        this.addChild(this.nextBlock);
    }
    private addActiveBlock() {
        if (!this.nextType) {
            this.nextType = Math.floor(Math.random() * (this.nextLines.length + 3)) + 1;
        }
        this.activeBlock = new CircleBlock(0, 650, this.nextType, false);
        this.activeBlock.kinematic = true;
        this.addChild(this.activeBlock);
        this.generateActiveBlock();
    }
    private drawMouse() {
        if (this.mousePressed) {
            const centerY = 650;
            const len = 1200;
            const boundX = 315;
            const p = this.toLocal(app.renderer.plugins.interaction.eventData.data.global);
            const angle= Math.atan2((p.y-centerY), p.x);
            this.preview.clear();
            this.preview.lineStyle(4,0x000000, 0.7);
            this.preview.moveTo(0, centerY);
            let xx = len*Math.cos(angle);
            let yy = centerY+len*Math.sin(angle);
            if (xx < -boundX || xx > boundX) {
                const d = Math.abs(xx)/xx * boundX / Math.cos(angle);
                xx = d*Math.cos(angle);
                yy = centerY+d*Math.sin(angle);
                this.preview.lineTo(xx,yy);
                xx -= (len-d)*Math.cos(-angle);
                yy -= (len-d)*Math.sin(-angle);
                this.preview.lineTo(xx, yy);
            } else {
                this.preview.lineTo(xx,yy);
            }
            
        }
    }
    protected onTapStart(e) {
        const lp = this.toLocal(e.data.global);
        if (this.isTransition || gameSettings.paused || !this.activeBlock || this.isActivePowerup || lp.y > 800) return;
        this.mousePressed = true;
        this.drawMouse();
    }
    protected onClick(e) {
        this.preview.clear();
        const lp = this.toLocal(e.data.global);
        if (this.isTransition || gameSettings.paused || !this.activeBlock || this.isActivePowerup || lp.y > 800) return;
        this.activeBlock.createCircleBody();
        const a = this.activeBlock;
        this.activeBlock = undefined;

        const angle= Math.atan2((lp.y-650) / METER_SIZE, lp.x / METER_SIZE);
        a.body.type = Body.KINEMATIC;
        const speed = 10;
        a.body.velocity = [speed*Math.cos(angle),speed*Math.sin(angle)];
        this.mousePressed = false;
        gsap.delayedCall(0.2, ()=> {
           this.addActiveBlock();
        });
    }
    private generateLineBlocks(lines: number) {
        this.previewBlocks.removeChildren();
        let yy = -620 - lines*70;
        this.nextLines = [];
        for (let j = 0; j < lines; j++) {
            let xx = -300;
            this.nextLines[j]=[];
            for (let i = 0; i < 9; i++) {
                this.nextLines[j][i] = Math.floor(Math.random() * this.level) + 1;
                const f = new CircleBlock(xx,yy,this.nextLines[j][i],false);
                this.previewBlocks.addChild(f);
                xx += 74;
            }
            yy+=70;
        }
    }
    private addLineBlocks() {
        this.previewBlocks.removeChildren();
        let x;
        let y = -500;
        for (let j = 0; j < this.nextLines.length; j++) {
            x = -300;
            for (let i = 0; i < this.nextLines[j].length; i++) {
                const f = new CircleBlock(x,y,this.nextLines[j][i]);
                this.addChild(f);
                x += 74;
            }
            y+=70
        }
    }
    private async generateNewBlocks() {
        const delay = async(t: number)=> {
            return new Promise<void>((resolve)=> {
                gsap.delayedCall(t, ()=> {
                    resolve();
                })
            });
        }
        for (let i = 0; i < 70; i++) {
            const type = Math.floor(Math.random() * this.level) + 1;
            const f = new CircleBlock(Math.random()*50-25,900,type);
            this.addChild(f);
            await delay(0.001);
        }
        await delay(1);
        const f = new GoalBlock(0,1100,CircleBlock.goalType);
        f.on("gameover", this.onGameOver);
        this.addChild(f);
        await delay(1);
        this.createBottomExit();
    }
    private onGameOver() {
        stateManager.start(MenuState);
    }
    private async generateStartBlocks() {
        this.createMoveWalls();
        const leftWall = new StaticBlock(-400, -50, 100, 1800, 0);
        this.addChild(leftWall);
        const rightWall = new StaticBlock(400, -50, 100, 1800, 0);
        this.addChild(rightWall);

        const bottomWall = new StaticBlock(-225, 800, 250, 100, 0);
        this.addChild(bottomWall);
        const bottomWall1 = new StaticBlock(225, 800, 250, 100, 0);
        this.addChild(bottomWall1);
        const bottomWall2 = new StaticBlock(-150, 1100, 100, 500, 0);
        this.addChild(bottomWall2);
        const bottomWall3 = new StaticBlock(150, 1100, 100, 500, 0);
        this.addChild(bottomWall3);
        const bottomWall4 = new StaticBlock(0, 1300, 200, 100, 0);
        this.addChild(bottomWall4);

        await this.generateNewBlocks();
        gameSettings.started = true;
    }
    private createMoveWalls() {
        this.moveBox = new StaticBlock(0, -575, 700, 50, 0);
        this.moveBox.type = Block.endType;
        this.addChild(this.moveBox);
        this.staticBox = new StaticBlock(0, -625, 700, 50, 0);
        this.addChild(this.staticBox);
        //const wall = new StaticBlock(0, -575, 700, 50, 0);
        //this.addChild(wall);
    }
    private createBottomExit() {
        this.bottomOpenWall = new StaticBlock(0, 800, 200, 100, 0);
        this.addChild(this.bottomOpenWall);
    }
    public close(): void {
        app.stage.off("pointerup");
        app.stage.off("pointerdown");
        Ticker.shared.remove(this.update);

        this.moveBox && this.moveBox.clean();
        this.staticBox && this.staticBox.clean();
        this.bottomOpenWall && this.bottomOpenWall.clean();
        this.activeBlock && this.activeBlock.clean();
        this.tick = 0;
        this.moving = false;
        this.isActivePowerup = false;
        this.preview.clear();
        this.mousePressed = false;
        this.previewBlocks && this.previewBlocks.removeChildren();
        this.nextBlock && this.nextBlock.clean();
        
    }
}