import { Sprite } from "pixi.js";
import { PhysicsSprite } from "../../../core/PhysicsSprite";
import gsap from "gsap";
export class Block extends PhysicsSprite {
    public static goalType = 1000;
    public static endType = 500;
    public sameColorNeighbors: Map<Block, boolean> = new Map<Block, boolean>();
    public difColorNeighbors: Map<Block, boolean> = new Map<Block, boolean>();
    public type: number;
    protected static stepsAmount = 3;
    protected static scaleFactor = 0.5;
    public steps = 1;
    public kinematic = false;
    protected busy = false;
    constructor(view: Sprite) {
        super(view);
    }
    public animateAndDestroy() {
        gsap.killTweensOf(this.view.scale);
        gsap.to(this.view.scale, {x: 0, y: 0, duration: 0.2, onComplete: ()=> {
            this.clean();
        }});
        this.destroyBody();
    }
    override clean(): void {
        this.busy = false;
        super.clean();
    }
    public boom() {
        this.increase(Block.stepsAmount-this.steps, true);
    }
    public correctType() {
        return this.type !== 0 && this.type !== Block.goalType && this.type !== Block.endType;
    }
    public increase(s: number, fromBoom: boolean = false) {
        if (this.busy || !this.body) return;
        this.busy = true;
        s += Math.max(0, this.sameColorNeighbors.size - 1);
        this.steps += s;
        if (this.steps >= Block.stepsAmount) {
            gsap.killTweensOf(this.view.scale);
            this.sameColorNeighbors.forEach((n,k)=> {
                if (k !== this) {
                    k.increase(Block.stepsAmount-k.steps, fromBoom);
                }
            });
            gsap.to(this.view.scale, {x: 0, y: 0, duration: 0.2, onComplete: ()=> {
                this.clean();
            }});
            this.destroyBody(fromBoom);
        }
    }
}