import * as GA from 'gameanalytics';
import { initSocial } from './core/natives/SocialManager';
import Game from './game/Game';
export enum EGAAdAction {
    Undefined = 0,
    Clicked = 1,
    Show = 2,
    FailedShow = 3,
    RewardReceived = 4
}
export enum EGAAdError {
    Undefined = 0,
    Unknown = 1,
    Offline = 2,
    NoFill = 3,
    InternalError = 4,
    InvalidRequest = 5,
    UnableToPrecache = 6
}
export enum EGAAdType {
    Undefined = 0,
    Video = 1,
    RewardedVideo = 2,
    Playable = 3,
    Interstitial = 4,
    OfferWall = 5,
    Banner = 6
}
export enum EGAProgressionStatus {
    Undefined = 0,
    Start = 1,
    Complete = 2,
    Fail = 3
}
GA.GameAnalytics.setEnabledInfoLog(false);
export let live: boolean = false;
export let lang = "en";
window.onfocus = () => {
    Howler.mute(false);
}
window.onblur = () => {
    Howler.mute(true);
}
window.onload = () => {
    initSocial()
        .then((obj)=> {
            lang = obj.language;
            live = obj.success;
            start(obj.gameKey, obj.secretKey);
        })
        .catch((e)=>{
            lang = "en";
            live = false;
            start("858dec355a72c9195a2a9d7b5b236599", "da73f2e7aa8cb82c345aa88235b57abb415e1cd5");
        });
}
function start(gameKey: string, sKey: string) {
    GA.GameAnalytics.initialize(gameKey, sKey);
    console.log(`Network: ${process.env.NET}, live: ${live}`);
    sendEvent("set", "network", process.env.NET, live ? 1 : 0);
    sendEvent("start", "app", "game");
    new Game();
}
export function sendValueEvent(name: string, category: string, value:number) {
    GA.GameAnalytics.addDesignEvent(`${name}:${category}`, value);
    gtag("event", name, {
        "event_category": category,
        "value": value
      });
}
export function sendProgressionEvent(status: EGAProgressionStatus, value?:number, date?: string) {
    GA.GameAnalytics.addProgressionEvent(status, "level", date, undefined, value);
    gtag("event", "progression", {
        "event_category": status === EGAProgressionStatus.Start ? "start" : (status !== EGAProgressionStatus.Fail ? "complete" : "fail"),
        "value": value
      });
}
export function sendAdEvent(action: EGAAdAction, type: EGAAdType, error?: EGAAdError) {
    if (error === undefined) {
        GA.GameAnalytics.addAdEvent(action, type, process.env.NET, "ad");
    } else {
        GA.GameAnalytics.addAdEventWithNoAdReason(action, type, process.env.NET, "ad", error);
    }
    let name = "";
    let category = "";
    switch (type) {
        case EGAAdType.Interstitial:
            category = "inter";
            break;
        case EGAAdType.RewardedVideo:
            category = "reward";
            break;
    }
    switch (action) {
        case EGAAdAction.FailedShow:
            name = "showError";
            break;
        case EGAAdAction.Show:
            name = "show";
            break;
        case EGAAdAction.RewardReceived:
            name = "success";
            break;
    }
    gtag("event", name, {
        "event_category": category,
      });
}
export function sendEvent(name: string, category: string, label: string, value:number = 0) {
    GA.GameAnalytics.addDesignEvent(`${name}:${category}:${label}`, value);
    gtag("event", name, {
        "event_category": category,
        "event_label": label,
        "value": value
      });
}
export function sendError(error: any) {
    const e = JSON.stringify(error);
    GA.GameAnalytics.addErrorEvent(GA.EGAErrorSeverity.Error, e);
    gtag("event", "show", {
        "event_category": "error",
        "event_label": "error",
        "value": e
      });
}
export function setLive(l: boolean) {
    live = l;
}
