import { sendEvent } from "../..";
import { gameSettings, stateManager } from "../../core/BaseGame";
import { AdType, shouldPlayFirstAds, showADS } from "../../core/natives/SocialManager";
import { BaseState } from "../../core/states/BaseState";
import { TextedButton } from "../../core/ui/TextedButton";
import { GameViewState } from "./GameViewState";

export class MenuState extends BaseState {
    public create(): void {
        const playBtn = new TextedButton("PLAY", 100, 0x000000, 0xFFFFFF);
        playBtn.position.y = gameSettings.originalHeight / 3 - 30;
        this.addChild(playBtn);
        playBtn.on("pointerup", () => {
            sendEvent("click", "menu", "play");
            this.onPlayClicked();
        });
    }
    public start(...params: any): void {
        sendEvent("start", "menu", "level");
    }
    public close(): void {
        sendEvent("close", "menu", "level");
    }
    private async onPlayClicked() {
        if (shouldPlayFirstAds()) {
            await showADS(AdType.Interstitial);
        }
        stateManager.start(GameViewState);
    }
}